  background: #333;
}
.theme-dark .button .button-inset:active, .theme-dark .button .button-inset.active,
.theme-dark .button-small:active,
.theme-dark .button-small.active,
.theme-dark .button-medium:active,
.theme-dark .button-medium.active {
  background: #000;
}
.theme-dark .button {
  box-shadow: inset 3px 3px 0 rgba(102, 102, 102, 0.8), inset -3px -3px 0 rgba(0, 0, 0, 0.5), 2px 2px 0 #000;
}
.theme-dark .button:active, .theme-dark .button.active {
  box-shadow: inset 2px 2px 0 #000, 1px 1px 0 #555, inset -2px -2px 0 #777, inset 4px 4px 0 #474747;
}
.theme-dark .button-medium {
  box-shadow: 2px 2px #000, inset -2px -2px rgba(0, 0, 0, 0.5), inset 2px 2px rgba(102, 102, 102, 0.8);
}
.theme-dark .button-medium:active, .theme-dark .button-medium.active {
  box-shadow: inset 2px 2px 0 #000, 1px 1px 0 #555, inset -2px -2px 0 #777, inset 4px 4px 0 #474747;
}
.theme-dark .button-small {
  box-shadow: inset -2px -2px 0 rgba(0, 0, 0, 0.5), inset 2px 2px 0 rgba(102, 102, 102, 0.8);
}
.theme-dark .button-small:active, .theme-dark .button-small.active {
  box-shadow: inset 2px 2px 0 #000, 1px 1px 0 #555, inset -2px -2px 0 #666;
}
.theme-dark .button-inset {
  box-shadow: 2px 2px 0 rgba(102, 102, 102, 0.6), inset 2px 2px rgba(0, 0, 0, 0.75);
}
.theme-dark .button-inset:active, .theme-dark .button-inset.active {
  background: #e6e6e6;
  box-shadow: inset 2px 2px 0 #000, 1px 1px 0 #666, inset -2px -2px 0 #666;
}
.theme-dark .startbar {
  background: #333;
  box-shadow: inset 0 4px 0 #666;
}
.theme-dark .startbar .programs:before {
  border-left: 2px solid rgba(102, 102, 102, 0.8);
  border-right: 2px solid rgba(0, 0, 0, 0.5);
}
.theme-dark .startbar a,
.theme-dark .startbar button {
