}
.theme-dark .start-cupboard {
  background: #333;
  box-shadow: -3px -3px 0 #666, inset -3px -3px 0 rgba(0, 0, 0, 0.5);
}
.theme-dark .start-cupboard a,
.theme-dark .start-cupboard button {
  color: #fff;
}
.theme-dark .start-cupboard a:hover,
.theme-dark .start-cupboard button:hover {
  background: blue;
  color: #fff;
}
.theme-dark .start-cupboard .shutdown {
  border-top: 1px solid #000;
  box-shadow: inset 0 1px 0 rgba(102, 102, 102, 0.8);
}
.theme-dark .program {
  background: #333;
  box-shadow: -3px -3px 0 #666, inset -3px -3px 0 rgba(0, 0, 0, 0.5);
}
.theme-dark .program:not(.system) .content {
  box-shadow: 3px 3px 0 rgba(0, 0, 0, 0.6), inset 3px 3px rgba(102, 102, 102, 0.5);
}
.theme-dark .toolbar button.disabled {
  color: #1a1a1a;
  text-shadow: 1px 1px #666;
}
.theme-dark .userInput .field {
  box-shadow: 3px 3px 0 rgba(102, 102, 102, 0.6), inset 3px 3px rgba(0, 0, 0, 0.75);
}
.theme-dark .userInput.hidden .option-toggle {
  color: #0d0d0d;
  text-shadow: 1px 1px #ccc;
}
.theme-dark .clock {
  color: #fff;
  box-shadow: inset 3px 3px 0 rgba(0, 0, 0, 0.5), 2px 2px 0 rgba(102, 102, 102, 0.8);
}
.theme-dark .settings,
.theme-dark .notification {
  color: #fff;
}
.theme-dark .settings a,
.theme-dark .notification a {
  background: #bdbdbd;
  padding: 2px;
}

