.startbar {
  display: none;
}
@media (min-width: 750px) {
  .startbar {
    display: flex;
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    padding: 8px 0 5px 8px;
    z-index: 3;
  }
  .startbar a img,
  .startbar button img {
    max-height: 20px;
    max-width: 25px;
    width: auto;
  }
  .startbar a img,
  .startbar a span,
  .startbar button img,
  .startbar button span {
    display: inline-block;
    vertical-align: middle;
  }
  .startbar .start {
    float: left;
  }
  .startbar .programs {
    display: flex;
    flex: 1;
    margin-left: 10px;
    padding-left: 10px;
    position: relative;
  }
  .startbar .programs:before {
    position: absolute;
    top: 1px;
    left: 0;
    display: block;
    content: "";
    height: 100%;
    width: 1px;
  }
  .startbar .programs a:not(.active),
  .startbar .programs button:not(.active) {
    font-weight: 400;
  }
  .startbar .programs a,
  .startbar .programs button {
    margin-right: 12px;
    min-width: 105px;
    text-align: center;
  }
  .startbar .programs .startbar-settings {
    width: 128px;
  }
  .startbar .closed-program {
    display: none;
  }
}
.startbar a img,
.startbar button img {
  margin-right: 5px;
}

.start-cupboard {
  position: fixed;
  left: 11px;
  bottom: 48px;
  width: 275px;
  padding: 0 3px 3px 0;
  display: none;
  z-index: 3;
}
.start-cupboard.visible {
  display: block;
}
.start-cupboard a,
.start-cupboard button {
  display: block;
  width: 100%;
  padding: 9px 7px 15px 10px;
  text-align: left;
}
.start-cupboard a img,
.start-cupboard a span,
.start-cupboard button img,
.start-cupboard button span {
  display: inline-block;
  vertical-align: middle;
}
.start-cupboard a span,
.start-cupboard button span {
  padding-top: 9px;
}
.start-cupboard a img,
.start-cupboard button img {
  max-width: 30px;
  margin-right: 5px;
}

